import React, {useContext} from 'react';
import DescriptionCSS from "../Description.module.scss"
import {GasContext} from "../../../context";
import {calculateTotalGaz} from "../../../utility/functions";


const Capacity = () => {

    let {
        gasCapacity
    } = useContext(GasContext);

    return (
        <div className={DescriptionCSS.Description}>
            <h2>Количество литров</h2>
            <small className="text-muted text-lowercase">Общее количество проданных литров</small>
            <hr/>
            <table className="table table-hover text-center">
                <thead>
                <tr>
                    <th scope="col"></th>
                    <th scope="col">Начало смены (Л)</th>
                    <th scope="col">Конец смены (Л)</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                    <th scope="row">Колонка ПНБ</th>
                    <td>{gasCapacity.startPNB}</td>
                    <td>{gasCapacity.endPNB}</td>
                </tr>
                <tr>
                    <th scope="row">Колонка АВТО</th>
                    <td>{gasCapacity.startVehicle}</td>
                    <td>{gasCapacity.endVehicle}</td>
                </tr>
                <tr>
                    <th scope="row">Уровнемер</th>
                    <td colSpan={2}>{gasCapacity.level}%</td>
                </tr>
                <tr>
                    <th scope="row">Всего</th>
                    <td colSpan={2} className="table-active">{calculateTotalGaz(gasCapacity)}</td>
                </tr>
                </tbody>
            </table>
        </div>
    );
};

export default Capacity;