import React, {useState} from 'react';
import Crop from "../components/Crop/Crop";
import {Container} from "react-bootstrap";
import {Navigation, Pagination, Scrollbar, A11y} from 'swiper';
import {Swiper, SwiperSlide} from "swiper/react";
import 'swiper/scss';
import 'swiper/scss/navigation';
import 'swiper/scss/pagination';
import 'swiper/scss/scrollbar';
import Capacity from "../components/Slides/Report/Capacity";
import {GasContext} from "../context";
import Sales from "../components/Slides/Report/Sales";
import AdditionalSales from "../components/Slides/Report/AdditionalSales";
import ParametersSlide from "../components/Slides/Report/Parameters";
import ParametersDescription from "../components/Descriptions/Gaz/Parameters";

const Report = () => {

    const [gasCapacity, setGasCapacity] = useState({
        startPNB: 0,
        endPNB: 0,
        startVehicle: 0,
        endVehicle: 0,
        level: 0
    })

    const [parameters, setParameters] = useState({
        cardPrice: 0,
        cashPrice: 0,
        cashStart: 0,
        salary: 0,
        collection: 0,
        terminalCash: 0,
    })

    const [sales, setSales] = useState([])
    const [equipments, setEquipments] = useState([])

    return (
        <div className="Report">
            <Crop>Отчёт</Crop>
            <Container fluid>
                <GasContext.Provider value={{
                    gasCapacity,
                    setGasCapacity,
                    sales,
                    setSales,
                    equipments,
                    setEquipments,
                    parameters,
                    setParameters
                }}>
                    <Swiper
                        modules={[Navigation, Pagination, Scrollbar, A11y]}
                        spaceBetween={50}
                        slidesPerView={1}
                        navigation
                        pagination={{clickable: true}}
                        className="pt-5 pb-5 px-4"
                        /*onSlideChange={(s) => {
                            window.scrollTo(0, s.$el[0].offsetTop + s.$el[0].clientTop / 3)
                        }}*/
                    >
                        <SwiperSlide>
                            <ParametersSlide/>
                        </SwiperSlide>

                        <SwiperSlide>
                            <Capacity/>
                        </SwiperSlide>

                        <SwiperSlide>
                            <Sales/>
                        </SwiperSlide>

                        <SwiperSlide>
                            <AdditionalSales/>
                        </SwiperSlide>

                        <SwiperSlide>
                            <ParametersDescription/>
                        </SwiperSlide>
                    </Swiper>
                </GasContext.Provider>
            </Container>
        </div>
    );
};

export default Report;