export const calculateTotalGaz = (gasCapacity) => (gasCapacity.endPNB - gasCapacity.startPNB) + (gasCapacity.endVehicle - gasCapacity.startVehicle)
export const replaceDot = (value) => value.replace(/,/g, ".")
export const calculateTotalReport = (context) => {
    let {
            gasCapacity: gaz,
            equipments,
            sales,
            parameters: {
                cardPrice,
                cashPrice,
                cashStart,
                salary,
                collection,
                terminalCash,
            },
        } = context,
        totalGaz = calculateTotalGaz(gaz),
        totalCash = 0,
        terminalTotalGaz = 0,
        corporation = 0,
        equipmentCash = 0,
        additionalGazTerminal = 0,
        additionalGazCash = 0,
        exportTerminalCash = terminalCash

    for (let sale of sales) {
        if (sale.payment === 'Безналичные') {
            terminalCash -= sale.litre * sale.price
            totalGaz -= sale.litre
            additionalGazTerminal += +sale.litre
        } else if (sale.payment === 'Корпоративные') {
            totalGaz -= sale.litre
            corporation += +sale.litre
        } else {
            additionalGazCash += +sale.litre
            totalCash += +totalCash + sale.litre * sale.price
        }
    }

    for (let equipment of equipments) {
        if (equipment.payment === 'Безналичные') {
            terminalCash -= equipment.price
        } else {
            totalCash += +equipment.price
        }
        equipmentCash += +equipment.price
    }

    terminalTotalGaz = terminalCash / cardPrice
    totalGaz -= terminalTotalGaz

    totalCash += (totalGaz * cashPrice) + +cashStart - salary - collection

    return {
        gasCapacity: calculateTotalGaz(gaz),
        equipments,
        sales,
        cardPrice,
        cashPrice,
        cashStart,
        salary,
        collection,
        exportTerminalCash,
        terminalTotalGaz,
        totalGaz,
        totalCash,
        additionalGazTerminal,
        additionalGazCash,
        corporation,
        equipmentCash
    }
}