import React, {useContext, useState} from 'react';
import {Button, Col, Row, Form} from "react-bootstrap";
import GasReportInput from "../../UI/Inputs/GasReportInput";
import AdditionalSalesDescription from "../../Descriptions/Gaz/AdditionalSales";
import {GasContext} from "../../../context";
import {replaceDot} from "../../../utility/functions";

const AdditionalSales = () => {

    const {
        equipments,
        setEquipments
    } = useContext(GasContext)

    const [optionsPayment] = useState([
        {id: Date.now() + Math.random(), name: 'Безналичные', value: 'Безналичные'},
        {id: Date.now() + Math.random(), name: 'Наличные', value: 'Наличные'},
    ])

    const [name, setName] = useState('')
    const [price, setPrice] = useState('')
    const [payment, setPayment] = useState(optionsPayment[0].name)

    const createEquipment = () => {
        let equipment = {
            id: Date.now() + Math.random(),
            name,
            price,
            payment
        }
        setEquipments([...equipments, equipment])
    }

    return (
        <Row className="pt-2 px-4">
            <Col sm={12} lg={12}>
                <h2>Продажа оборудования</h2>
                <small className="text-muted text-lowercase">Введи проданное оборудование</small>
                <hr/>
                <Row>
                    <div className="form-group col-6 col-sm-6 col-md-2">
                        <GasReportInput
                            htmlFor="equipmentPrice"
                            label="Цена"
                            placeholder="0"
                            value={price}
                            onChange={event => setPrice(replaceDot(event.target.value))}
                        />
                    </div>

                    <div className="form-group col-6 col-sm-6 col-md-5">
                        <GasReportInput
                            type="text"
                            htmlFor="equipmentName"
                            label="Название"
                            placeholder="Шланг"
                            value={name}
                            onChange={event => setName(event.target.value)}
                        />
                    </div>

                    <div className="form-group col-12 col-sm-6 col-md-3">
                        <br/>
                        <Form.Select value={payment} onChange={event => setPayment(event.target.value)}>
                            <option disabled={true}>Способ оплаты</option>
                            {optionsPayment.map((option) => (
                                <option key={option.id} value={option.value}>{option.name}</option>
                            ))}
                        </Form.Select>
                    </div>

                    <div className="form-group col-12 col-sm-12 col-md-2">
                        <br/>
                        <Button variant="primary" className="w-100" onClick={createEquipment}>+</Button>
                    </div>
                </Row>
            </Col>

            <Col sm={12} lg={12}>
                <AdditionalSalesDescription/>
            </Col>
        </Row>
    );
};

export default AdditionalSales;