import React from 'react';

const GasReportInput = (p) => {
    return (
        <React.Fragment>
            <label htmlFor={p.htmlFor}>{p.label}</label>
            <input type={p.type ? p.type : "number"}
                   tabIndex={p.tab}
                   className="form-control has-validation"
                   id={p.htmlFor}
                   placeholder={p.placeholder}
                   value={p.value}
                   onChange={p.onChange}
            />
            <div className="invalid-feedback">
                Please choose a username.
            </div>
        </React.Fragment>
    );
};

export default GasReportInput;