import React, {useContext} from 'react';
import {Col, Row} from "react-bootstrap";
import GasReportInput from "../../UI/Inputs/GasReportInput";
import {replaceDot} from "../../../utility/functions";
import {GasContext} from "../../../context";

const Parameters = () => {

    const {
        parameters,
        setParameters
    } = useContext(GasContext)


    return (
        <Row className="pt-2 px-4">
            <Col sm={12} lg={12}>
                <h2>Параметры</h2>
                <small className="text-muted text-lowercase">Основные параметры</small>
                <hr/>
                <Row>
                    <div className="form-group col-12 col-md-12 col-sm-12">
                        <GasReportInput
                            htmlFor="cardPrice"
                            label="Цена за карты"
                            placeholder="0"
                            value={parameters.cardPrice}
                            onChange={event => setParameters({...parameters, cardPrice: replaceDot(event.target.value)})}
                        />
                    </div>

                    <div className="form-group col-12 col-md-12 col-sm-12">
                        <GasReportInput
                            htmlFor="cashPrice"
                            label="Цена за наличные"
                            placeholder="0"
                            value={parameters.cashPrice}
                            onChange={event => setParameters({...parameters, cashPrice: replaceDot(event.target.value)})}
                        />
                    </div>

                    <div className="form-group col-12 col-md-12 col-sm-12">
                        <GasReportInput
                            htmlFor="cashStart"
                            label="Касса на начало смены"
                            placeholder="0"
                            value={parameters.cashStart}
                            onChange={event => setParameters({...parameters, cashStart: replaceDot(event.target.value)})}
                        />
                    </div>

                    <div className="form-group col-12 col-md-12 col-sm-12">
                        <GasReportInput
                            htmlFor="salary"
                            label="Зарплата"
                            placeholder="0"
                            value={parameters.salary}
                            onChange={event => setParameters({...parameters, salary: replaceDot(event.target.value)})}
                        />
                    </div>

                    <div className="form-group col-12 col-md-12 col-sm-12">
                        <GasReportInput
                            htmlFor="collection"
                            label="Инкассация"
                            placeholder="0"
                            value={parameters.collection}
                            onChange={event => setParameters({...parameters, collection: replaceDot(event.target.value)})}
                        />
                    </div>

                    <div className="form-group col-12 col-md-12 col-sm-12">
                        <GasReportInput
                            htmlFor="terminalCash"
                            label="Безналичные с терминала"
                            placeholder="0"
                            value={parameters.terminalCash}
                            onChange={event => setParameters({...parameters, terminalCash: replaceDot(event.target.value)})}
                        />
                    </div>
                </Row>
            </Col>
        </Row>
    );
};

export default Parameters;