import React, {useContext} from 'react';
import DescriptionCSS from "../Description.module.scss"
import {GasContext} from "../../../context";


const AdditionalSales = () => {

    const {
        equipments,
        setEquipments
    } = useContext(GasContext)

    const deleteEquipment = (equipmentID) => {
        setEquipments(equipments.filter(e => e.id !== equipmentID))
    }

    return (
        <div className={DescriptionCSS.Description}>
            <h2>Проданное оборудование</h2>
            <small className="text-muted text-lowercase">Список проданного оборудования</small>
            <hr/>
            <table className="table table-hover text-center">
                <thead>
                <tr>
                    <th>Наименование</th>
                    <th>Цена</th>
                    <th>Оплата</th>
                </tr>
                </thead>
                <tbody>
                {
                    equipments.length !== 0
                        ?
                        equipments.map(equipment =>
                            <tr onClick={() => deleteEquipment(equipment.id)} key={equipment.id}>
                                <td>{equipment.name}</td>
                                <td>{equipment.price}</td>
                                <td>{equipment.payment}</td>
                            </tr>
                        )
                        :
                        <tr>
                            <td colSpan={4} className="table-active">Нет данных</td>
                        </tr>
                }
                </tbody>
            </table>
        </div>
    );
};

export default AdditionalSales;