import React, {useContext} from 'react';
import DescriptionCSS from "../Description.module.scss"
import {GasContext} from "../../../context";

const Sales = () => {

    const {
        sales,
        setSales
    } = useContext(GasContext);

    const deleteSale = (sale) => {
        setSales(sales.filter(s => s.id !== sale))
    }


    return (
        <div className={DescriptionCSS.Description}>
            <h2>Продажи</h2>
            <small className="text-muted text-lowercase">Дополнительные продажи газа</small>
            <hr/>
            <table className="table table-hover text-center">
                <thead>
                <tr>
                    <th>Фирма</th>
                    <th>Цена</th>
                    <th>Литры</th>
                    <th>Оплата</th>
                </tr>
                </thead>
                <tbody>
                {
                    sales.length !== 0
                        ?
                        sales.map(sale =>
                            <tr onClick={() => deleteSale(sale.id)} key={sale.id}>
                                <td>{sale.firm}</td>
                                <td>{sale.price}</td>
                                <td>{sale.litre}</td>
                                <td>{sale.payment}</td>
                            </tr>
                        )
                        :
                        <tr>
                            <td colSpan={4} className="table-active">Нет данных</td>
                        </tr>
                }

                </tbody>
            </table>

        </div>
    );
};

export default Sales;