import React, {useContext, useState} from 'react';
import {Col, Row, Form, Button} from "react-bootstrap";
import SalesDescription from "../../Descriptions/Gaz/Sales";
import GasReportInput from "../../UI/Inputs/GasReportInput";
import {GasContext} from "../../../context";
import {replaceDot} from "../../../utility/functions";

const Sales = () => {

    const {
        sales,
        setSales,
    } = useContext(GasContext)

    const [optionsNameFirms] = useState([
        {id: Date.now() + Math.random(), name: 'YANDEX', value: 'YANDEX'},
        {id: Date.now() + Math.random(), name: 'Алгоритм', value: 'Алгоритм'},
        {id: Date.now() + Math.random(), name: 'Феникс', value: 'Феникс'},
        {id: Date.now() + Math.random(), name: 'Веллдорис', value: 'Веллдорис'},
        {id: Date.now() + Math.random(), name: 'Всеволожский Мясной Двор', value: 'Всеволожский Мясной Двор'},
        {id: Date.now() + Math.random(), name: 'Другое', value: 'Другое'},
    ])
    const [optionsPayment] = useState([
        {id: Date.now() + Math.random(), name: 'Безналичные', value: 'Безналичные'},
        {id: Date.now() + Math.random(), name: 'Корпоративные', value: 'Корпоративные'},
        {id: Date.now() + Math.random(), name: 'Наличные', value: 'Наличные'},
    ])

    const [firm, setFirm] = useState(optionsNameFirms[0].name)
    const [price, setPrice] = useState('')
    const [litre, setLitre] = useState('')
    const [payment, setPayment] = useState(optionsPayment[0].name)

    const createSale = () => {
        let sale = {
            id: Date.now() + Math.random(),
            firm,
            price,
            litre,
            payment,
        }
        setSales([...sales, sale])
    }

    return (
        <Row className="pt-2 px-4">
            <Col sm={12} lg={12}>
                <h2>Корпоративные продажи</h2>
                <small className="text-muted text-lowercase">Добавьте наименования</small>
                <hr/>
                <Row>
                    <div className="form-group col-6 col-sm-6 col-md-2">
                        <GasReportInput
                            htmlFor="priceGas"
                            label="Цена"
                            placeholder="0"
                            value={price}
                            onChange={event => setPrice(replaceDot(event.target.value))}
                        />
                    </div>

                    <div className="form-group col-6 col-sm-6 col-md-2">
                        <GasReportInput
                            htmlFor="litre"
                            label="Литры"
                            placeholder="0"
                            value={litre}
                            onChange={event => setLitre(replaceDot(event.target.value))}
                        />
                    </div>

                    <div className="form-group col-12 col-sm-6 col-md-3">
                        <br/>
                        <Form.Select value={firm} onChange={event => setFirm(event.target.value)}>
                            <option disabled={true}>Наименование</option>
                            {optionsNameFirms.map((option) => (
                                <option key={option.id} value={option.value}>{option.name}</option>
                            ))}
                        </Form.Select>
                    </div>

                    <div className="form-group col-12 col-sm-6 col-md-3">
                        <br/>
                        <Form.Select value={payment} onChange={event => setPayment(event.target.value)}>
                            <option disabled={true}>Способ оплаты</option>
                            {optionsPayment.map((option) => (
                                <option key={option.id} value={option.value}>{option.name}</option>
                            ))}
                        </Form.Select>
                    </div>

                    <div className="form-group col-12 col-sm-12 col-md-2">
                        <br/>
                        <Button variant="primary" className="w-100" onClick={createSale}>+</Button>
                    </div>
                </Row>
            </Col>

            <Col sm={12} lg={12}>
                <SalesDescription/>
            </Col>
        </Row>
    );
};

export default Sales;