import React, {useContext} from 'react';
import {Col, Row} from "react-bootstrap";
import {GasContext} from "../../../context";
import GasReportInput from "../../UI/Inputs/GasReportInput";
import CapacityDescription from "../../Descriptions/Gaz/Capacity";
import {replaceDot} from "../../../utility/functions";

const Capacity = () => {

    let {
        gasCapacity,
        setGasCapacity
    } = useContext(GasContext)

    return (
        <Row className="pt-2 px-4">
            <Col sm={12} lg={12}>
                <h2>Счётчики</h2>
                <small className="text-muted text-lowercase">Показания со счётчиков</small>
                <hr/>
                <Row>
                    <div className="form-group col-12 col-sm-6 col-md-6">
                        <GasReportInput
                            tab="1"
                            htmlFor="startGasPNB"
                            label="Начало смены / колонка ПНБ"
                            placeholder="0"
                            value={gasCapacity.startPNB}
                            onChange={(e) => setGasCapacity({
                                ...gasCapacity,
                                startPNB: replaceDot(e.target.value)
                            })}
                        />
                    </div>

                    <div className="form-group col-12 col-sm-6 col-md-6">
                        <GasReportInput
                            tab="2"
                            htmlFor="endGasPNB"
                            label="Конец смены / колонка ПНБ"
                            placeholder="0"
                            value={gasCapacity.endPNB}
                            onChange={(e) => setGasCapacity({
                                ...gasCapacity,
                                endPNB: replaceDot(e.target.value)
                            })}
                        />
                    </div>


                    <div className="form-group col-12 col-sm-6 col-md-6">
                        <GasReportInput
                            tab="3"
                            htmlFor="startGasVehicle"
                            label="Начало смены / колонка АВТО"
                            placeholder="0"
                            value={gasCapacity.startVehicle}
                            onChange={(e) => setGasCapacity({
                                ...gasCapacity,
                                startVehicle: replaceDot(e.target.value)
                            })}
                        />
                    </div>

                    <div className="form-group col-12 col-sm-6 col-md-6">
                        <GasReportInput
                            tab="4"
                            htmlFor="endGasVehicle"
                            label="Конец смены / колонка АВТО"
                            placeholder="0"
                            value={gasCapacity.endVehicle}
                            onChange={(e) => setGasCapacity({
                                ...gasCapacity,
                                endVehicle: replaceDot(e.target.value)
                            })}
                        />
                    </div>

                    <div className="form-group col-12 col-sm-12 col-md-12">
                        <GasReportInput
                            tab="5"
                            htmlFor="level"
                            label="Уровнемер"
                            type="text"
                            placeholder="0"
                            value={gasCapacity.level}
                            onChange={(e) => setGasCapacity({
                                ...gasCapacity,
                                level: replaceDot(e.target.value)
                            })}
                        />
                    </div>
                </Row>
            </Col>

            <Col sm={12} lg={12}>
                <CapacityDescription/>
            </Col>
        </Row>
    );
};

export default Capacity;