import React, {useContext} from 'react';
import {GasContext} from "../../../context";
import {calculateTotalReport} from "../../../utility/functions";
import {Col, Row} from "react-bootstrap";
import cashboxPNG from '../../../assets/images/icons/cards/cashbox.png'
import salaryPNG from '../../../assets/images/icons/cards/salary.png'

const Parameters = () => {

    let {
        gasCapacity,
        equipments,
        sales,
        cardPrice,
        cashPrice,
        cashStart,
        salary,
        collection,
        exportTerminalCash,
        terminalTotalGaz,
        totalGaz,
        totalCash,
        additionalGazTerminal,
        additionalGazCash,
        corporation,
        equipmentCash
    } = calculateTotalReport(useContext(GasContext))

    return (
        <Row className="pt-2 px-4">
            <Col sm={12} lg={12}>
                <h2>Итого</h2>
                <small className="text-muted text-lowercase">Общие подсчёты</small>
                <hr/>
                <Row>
                    <div className="col-12 col-sm-4 col-md-4">
                        <div className="card mb-3 text-center">
                            <img
                                src={cashboxPNG}
                                className="card-img-top" alt="..."/>
                            <div className="card-body">
                                <h5 className="card-title">Касса</h5>
                                <p className="card-text">{cashStart} </p>
                                <p className="card-text"><small className="text-muted">На начало смены</small></p>
                            </div>
                        </div>
                    </div>

                    <div className="col-12 col-sm-4 col-md-4">
                        <div className="card mb-3 text-center">
                            <img
                                src={salaryPNG}
                                className="card-img-top" alt="..."/>
                            <div className="card-body">
                                <h5 className="card-title">Зарплата</h5>
                                <p className="card-text">{salary} </p>
                                <p className="card-text"><small className="text-muted"><br/></small></p>
                            </div>
                        </div>
                    </div>
                </Row>
                <table className="table table-hover text-center">
                    <thead>
                    <tr>
                        <th></th>
                        <th>Итого</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                        <th>Касса на начало смены</th>
                        <td>{cashStart}</td>
                    </tr>
                    <tr>
                        <th>Зарплата</th>
                        <td>{salary}</td>
                    </tr>
                    <tr>
                        <th>Инкассация</th>
                        <td>{collection}</td>
                    </tr>
                    <tr>
                        <th>Терминал</th>
                        <td>{exportTerminalCash}</td>
                    </tr>
                    <tr>
                        <th>Продано газа</th>
                        <td>{gasCapacity.toFixed(2)}</td>
                    </tr>
                    <tr>
                        <th>Оборудование</th>
                        <td>
                            {equipments.map((el) => (
                                <p>Наименование: <b>{el.name}</b> цена: <b>{el.price}</b> - {el.payment}</p>
                            ))}
                        </td>
                    </tr>
                    <tr>
                        <th>Доп. продажи газа</th>
                        <td>
                            {sales.map((el) => (
                                <p>Фирма: <b>{el.firm}</b> цена: <b>{el.price}</b> литры: <b>{el.litre}</b> - {el.payment}
                                </p>
                            ))}
                        </td>
                    </tr>
                    <tr>
                        <th>Терминал - газ по {cardPrice} </th>
                        <td>{terminalTotalGaz.toFixed(2)}</td>
                    </tr>
                    <tr>
                        <th>Наличные - газ по {cashPrice} </th>
                        <td>{totalGaz.toFixed(2)}</td>
                    </tr>
                    <tr>
                        <th>Литры доп. газ (терминал)</th>
                        <td>{additionalGazTerminal.toFixed(2)}</td>
                    </tr>
                    <tr>
                        <th>Литры доп. газ (наличные)</th>
                        <td>{additionalGazCash.toFixed(2)}</td>
                    </tr>
                    <tr>
                        <th>Литры корпоратив</th>
                        <td>{corporation.toFixed(2)}</td>
                    </tr>
                    <tr>
                        <th>Проданное оборудование</th>
                        <td>{equipmentCash.toFixed(2)}</td>
                    </tr>
                    <tr className="table-active">
                        <th>Наличные в кассе</th>
                        <td>{totalCash.toFixed(2)}</td>
                    </tr>
                    </tbody>
                </table>
            </Col>
        </Row>
    );
};

export default Parameters;